@font-face {
  font-family: ProximaNova-Regular;
  src: url(../fonts/ProximaNova-Regular.eot);
  src: url(../fonts/ProximaNova-Regular.eot?#iefix) format("embedded-opentype"),
    url(../fonts/ProximaNova-Regular.otf) format("otf"),
    url(../fonts/ProximaNova-Regular.ttf) format("truetype"),
    url(../fonts/ProximaNova-Regular.svg#ProximaNova-Regular) format("svg"),
    url(../fonts/ProximaNova-Regular.woff) format("woff"),
    url(../fonts/ProximaNova-Regular.woff2) format("woff2");
}
@font-face {
  font-family: ProximaNova-Bold;
  src: url(../fonts/ProximaNova-Bold.eot);
  src: url(../fonts/ProximaNova-Bold.eot?#iefix) format("embedded-opentype"),
    url(../fonts/ProximaNova-Bold.otf) format("otf"),
    url(../fonts/ProximaNova-Bold.svg#ProximaNova-Bold) format("svg"),
    url(../fonts/ProximaNova-Bold.ttf) format("truetype"),
    url(../fonts/ProximaNova-Bold.woff) format("woff"),
    url(../fonts/ProximaNova-Bold.woff2) format("woff2");
}
@font-face {
  font-family: "Proxima Nova Thin";
  src: url(../fonts/Proxima-Nova-Thin.eot);
  src: url(../fonts/Proxima-Nova-Thin.eot?#iefix) format("embedded-opentype"),
    url(../fonts/Proxima-Nova-Thin.woff2) format("woff2"),
    url(../fonts/Proxima-Nova-Thin.woff) format("woff"),
    url(../fonts/Proxima-Nova-Thin.ttf) format("truetype"),
    url(../fonts/Proxima-Nova-Thin.svg#Proxima%20Nova%20Thin) format("svg"),
    url(../fonts/Proxima-Nova-Thin.otf) format("otf");
}
body {
  font-family: ProximaNova-Regular;
  background-color: #f4f4f6;
  color: #000;
}
a {
  text-decoration: none;
}
.page-title {
  position: relative;
  margin-bottom: 40px;
  padding-left: 35px;
}
.slick-dots li button:before {
  font-size: 12px;
}
.page-title::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -1px;
  z-index: -1;
  width: 100%;
  height: 1px;
  background-color: #e3e3e3;
}
.page-title h3 {
  font-size: 40px;
  font-family: "Proxima Nova Thin";
  background-color: #f4f4f6;
  padding-left: 20px;
  padding-right: 10px;
  margin-bottom: 0;
}
.page-title h3 strong {
  font-family: ProximaNova-Bold;
}
.page-title .img-blook-icon {
  max-width: 100%;
  height: 48px;
  background-color: #f4f4f6;
  position: absolute;
  top: 0;
  left: 0;
}
.navbar-brand {
  margin: 0;
  cursor: pointer;
}
.head-seacrh-icon-main {
  cursor: pointer;
}
.header-topbar {
  padding: 10px 0;
  border-bottom: 1px solid #dedee0;
}
.header-topbar.fca-tagline {
  background-color: #293a66;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
}
.header-topbar.fca-tagline p {
  margin-bottom: 0;
  text-align: center;
  color: #fff;
}
.header-topbar.fca-tagline p a {
  color: #00b7e1;
  text-decoration: underline;
}

