.header-email a,
.header-phone a {
  color: #666;
}
.header-socialink ul {
  flex-direction: inherit;
}
.header-socialink ul li {
  padding: 0;
  font-size: 0;
  margin: 0 10px;
  border: none;
}
.header-socialink ul li:last-child {
  margin-right: 0;
}
.header-socialink ul li:first-child {
  margin-left: 0;
}
.header-socialink ul li a {
  display: block;
}
.header-socialink ul li a svg {
  height: 16px;
  width: 16px;
}
.header-navbar-main .navbar-collapse {
  flex-grow: inherit;
}
.header-navbar-main .navbar-nav a {
  color: #000;
  margin: 0 4px;
  font-size: 16px;
  line-height: 24px;
  text-transform: capitalize;
}
.header-navbar-main .navbar-nav a.pns{
  text-transform: initial;
}
.header-navbar-main .navbar-nav a.login-btn {
  color: #fff;
  border-radius: 8px;
  min-width: 100px;
  padding: 6px 10px;
  background-color: #293a66;
  border: none;
  margin-right: 0;
}
.header-navbar-main .navbar-nav a.login-btn:hover {
  background-color: rgb(41 58 102 / 90%);
}
.navbar-expand-lg .navbar-nav .nav-link.dropdown-toggle {
  margin-right: 0;
}
.navbar-expand-lg .navbar-nav #login-user.nav-link.dropdown-toggle {
  padding-right: 0 !important;
}
.header-search-main {
  margin-right: 30px;
}
.header-seacrh-lang-signup-main .btn {
  font-size: 16px;
  line-height: 24px;
  background: #00b7e1;
  border-radius: 10px;
  border-color: #00b7e1;
  color: #fff;
  padding: 8px 15px;
}
.header-seacrh-lang-signup-main .btn:hover {
  background: #293965;
  border-color: #293965;
}
.dropdown .dropdown-menu .dropdown-item {
  margin: 0;
}
.dropdown .dropdown-menu .dropdown-item:hover {
  margin: 0;
}

.benefit-working {
  padding: 80px 0;
}
.benefit-working-wrap .benefit-card-img {
  width: 60px;
  height: 60px;
  background-color: #1575a243;
  border-radius: 50%;
}
.benefit-working-wrap .benefit-working-card,
.vid-list-sec .vid-guid-left .video-is-completed {
  background-color: #fff;
  height: 100%;
  padding: 20px 25px;
  border-radius: 10px;
  border: 1px solid #e3e3e3;
}
.benefit-working-wrap .benefit-working-ctn > .row {
  row-gap: 24px;
}
.benefit-working-wrap .benefit-card-img .card-icon {
  width: 32px;
}
.benefit-working-wrap .benefit-card-ctn h4 {
  font-family: ProximaNova-Bold;
  margin-bottom: 5px;
}
.benefit-working-wrap .benefit-card-ctn p {
  margin-bottom: 0;
  letter-spacing: 0;
}
.benefit-card-ctn {
  width: calc(100% - 60px);
  padding-left: 20px;
}
.our-course .page-title {
  margin-bottom: 30px;
}
.course-card-img{
  position: relative;
}
.course-card-img .course-img-link,
.objectFit-img {
  max-width: 100%;
  position: absolute;
  object-fit: cover;
  object-position: center;
  top: 0;
  left: 0;
  width: 100%;
}
.course-card-img .course-img-link{
  height: 100%;
}
.course-card-img .course-img-link img{
  width: 100%;
  height: 100%;
}
.course-card-img::before {
  content: "";
  padding-top: 68%;
  display: block;
}
.br-client-sec,
.mt-policy-sec,
.mt-tnc-sec,
.mt-vid-guid-sec,
.mynotes-list-sec,
.our-course,
.telegram-section {
  padding: 50px 0 154px;
}
.mynotes-list-sec{
  padding-bottom: 154px;
}
.page-wrapper{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
}
/* .pricing_container {
  padding-bottom: 154px;
} */
.our-course-ctn .col-lg-4 {
  padding: 0 15px;
}
.our-course-wrap .our-course-card {
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  height: 100%;
}
.our-course-wrap .our-course-card .badge {
  position: absolute;
  right: 20px;
  top: 20px;
  z-index: 1;
  background: #293965;
  text-transform: uppercase;
  background: #293965 !important;
  text-transform: uppercase;
  font-weight: 400;
  font-family: ProximaNova-Regular;
  letter-spacing: 0.5px;
  font-size: 14px !important;
}
.our-course-card .course-card-ctn {
  padding: 0 20px 20px;
}
.our-course-wrap .course-card-ctn h4 {
  font-size: 16px;
  position: relative;
  padding-left: 25px;
  font-weight: 500;
  line-height: 24px;
}
.our-course-wrap .course-card-ctn h4 img {
  position: absolute;
  top: -2px;
  left: 0;
  height: 24px;
}
.our-course-wrap .course-card-ctn h3 {
  font-size: 24px;
  font-family: ProximaNova-Bold;
  margin-bottom: 12px;
}
.our-course-wrap .course-review {
  line-height: 20px;
  margin-bottom: 10px;
}
.our-course-wrap .course-review-separator {
  margin: 0 8px;
  font-family: ProximaNova-Bold;
}
.our-course-wrap .course-review-rating {
  padding-right: 24px;
  position: relative;
}
.our-course-wrap .course-review-rating::after {
  content: "";
  width: 15px;
  height: 15px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
}
.our-course-wrap .course-info li {
  background-color: transparent;
  margin-bottom: 2px;
  border: none;
  padding: 0 0 0 30px;
  position: relative;
}
.our-course-wrap .course-info li::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(data:image/svg+xml;base64,);
}
.our-course-wrap .course-details {
  color: #00b7e1;
  position: relative;
  margin-bottom: 0;
  font-family: ProximaNova-Bold;
}
.our-course-wrap .course-details::after {
  content: "";
  width: 18px;
  height: 20px;
  position: absolute;
  top: 50%;
  right: -30px;
  transform: translateY(-50%);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
}
.our-course-wrap .course-details:hover {
  text-decoration: underline;
}
.subscrib-container{
  position: absolute;
  bottom: auto !important;
  top: -104px;
  margin-top: 0;
  left: 0;
  right: 0;
}
.subscribe-us {
  padding: 28px 40px;
  background-color: #00b7e1;
  border-radius: 18px;  
  margin-left: auto;
  margin-right: auto;
  width: calc(100% - 24px);
  overflow: hidden;
}
.subscribe-us::before {
  content: "";
  position: absolute;
  width: 446px;
  height: 251px;
  left: 0;
  bottom: 0;
